@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  min-height: 100%;
}

body {
  background: #f8f8f8;
  /* background: #000 url('./assets/background.jpg') no-repeat;
  background-size: cover; */
  -webkit-font-smoothing: antialiased !important;
}

body, input, button {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-size: 14px;
}

li {
  list-style: none;
  margin-top: 10px;
}

h1 {
  font-size: 22px;
  margin-bottom: 10px;
}
div.header {
    background: #fff;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: left;
}

div.header h1 {
    margin-top: 7px;
    margin-left: 25px;
}

div.header form {
    display: flex;
    margin-left: 50px;
}

div.header form input {
    margin-bottom: 0px;
    background: #f0f0f0;
    border: 0;
    border-radius: 2px;
    height: 40px;
    padding: 0 15px;
    font-size: 16px;
    width: 100%;
}

div.header button.btn {
    border: 1px solid #bbb;
    border-radius: 5px;
    height: 40px;
    padding: 0 20px;
    font-size: 16px;
    font-weight: bold;
    background: #fff;
    color: #333;
    cursor: pointer;
    margin-left: 5px;
}

div.header button.btn:hover {
    border: 1px solid #555;
}
/**
* MAIN PAGE
*/
.rankingMain {
    background: #fff;
    padding: 20px;
    border-radius: 7px;
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 3;
}
@media (max-width: 840px) {
    .rankingMain {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
    }
}

.rankingMain > div {
    overflow-x: hidden; 
    overflow-x: auto;
    height: 92%;
}
.rankingMain table {
    border-collapse: collapse;
    width: 100%;
}

.rankingMain table th button img {
    float: right;
    margin-left: 5px;
    margin-top: 1px;
}
.rankingMain table td, .rankingMain table th {
    border-bottom: 1px solid #ddd;
    padding: 10px 15px;
}
  
.rankingMain table tr.item {
    text-decoration: none;
}

.rankingMain table tr.item:hover {
    background-color: #ddd;
    /* cursor: pointer; */
}
  
.rankingMain table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
}

.rankingMain table th button {
    background: none;
    border: none;
    font-weight: 700;
    cursor: pointer;
}


/**
* LOCAL PAGE
*/
.rankingLocal {
    background: #fff;
    padding: 20px;
    border-radius: 7px;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 4;
}
@media (max-width: 1240px) {
    .rankingLocal {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 3;
        grid-row-end: 5;
    }
}

.rankingLocal > div {
    overflow-x: hidden; 
    overflow-x: auto;
    height: 92%;
}
.rankingLocal table {
    border-collapse: collapse;
    width: 100%;
}

.rankingLocal table th button img {
    float: right;
    margin-left: 5px;
    margin-top: 1px;
}
.rankingLocal table td, .rankingLocal table th {
    border-bottom: 1px solid #ddd;
    padding: 10px 15px;
}
  
.rankingLocal table tr.item {
    text-decoration: none;
}

.rankingLocal table tr.item:hover {
    background-color: #ddd;
    /* cursor: pointer; */
}
  
.rankingLocal table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
}

.rankingLocal table th button {
    background: none;
    border: none;
    font-weight: 700;
    cursor: pointer;
}
.statsChartMain {
    background: #fff;
    padding: 20px;
    border-radius: 7px;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
}

.statsChartLocal {
    background: #fff;
    padding: 20px;
    border-radius: 7px;
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;
}
div.dashboardGlobal {
    display: grid;
    grid-template-columns: 300px 1fr 1fr 1fr 1fr;
    grid-template-rows: 250px 275px;
    grid-gap: 25px;
    margin: 25px;
}
  
@media (max-width: 1240px) {
    div.dashboardGlobal {
        grid-template-columns: 300px 1fr 1fr;
        grid-gap: 25px;
        margin: 25px;
    }
}
@media (max-width: 840px) {
    div.dashboardGlobal {
        grid-template-columns: 300px 1fr;
        grid-gap: 25px;
        margin: 25px;
    }
}

div.globalSummary {
    background: #fff;
    padding: 20px;
    border-radius: 7px;
}

div.globalSummary > strong {
    font-size: 15px;
}

div.globalSummary > p  {
    font-size: 28px;
    font-weight: 600;
    margin-top: 10px;
}

div.globalDetails {
    margin-top: 20px;
}

div.globalDetails li {
    margin-top: 20px;
}

.dataGlobal, .new {
    float: right;
}

.new {
    background: #eeeeee;
    border-radius: 3px;
    margin-left: 10px;
    font-size: 11px;
    color: #5f5f5f;
    padding: 2px 4px;
}
div.dashboardLocal {
    display: grid;
    grid-template-columns: 300px 1fr 1fr 1fr 1fr;
    grid-template-rows: 275px 275px 275px;
    grid-gap: 25px;
    margin: 25px;
  }
  
  @media (max-width: 1240px) {
    div.dashboardLocal {
      grid-template-columns: 300px 1fr 1fr;
      grid-template-rows: 275px 275px 275px 275px;
      grid-gap: 25px;
      margin: 25px;
    }
  }

div.virusSpreadChart {
    grid-column-start: 2;
    grid-column-end: 4;
    background: #fff;
    padding: 20px;
    border-radius: 7px;
}

div.nowActiveChart {
    grid-column-start: 4;
    grid-column-end: 6;
    background: #fff;
    padding: 20px;
    border-radius: 7px;
}
@media (max-width: 1240px) { 
    div.nowActiveChart {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
    }
}
